import React from "react"
import Layout from "../elements/layout"
import Header from "../elements/header/header"
import Container from "../elements/container/container"
import Blogcontent from "../elements/blogcontent/blogcontent"
import IllustrationBackgroundModern from "../background/background_3/backgroundmodern"
import Person from "../elements/person/contactblock"
import SEO from "../elements/seo"
import "./style.css"
import CTA from "../elements/cta/cta"
import rehypeReact from "rehype-react"
import Audio from "../elements/audio/audio"
import Button from "../elements/button/button"
import HeaderImage from "../elements/headerimg/headerimage"
import FancyBox from "../elements/fancy/fancy"
import Thumbnail from "../elements/thumbnail/thumbnail"
import Image from "../elements/image/image"
import BubbleBox from "../elements/bubblebox"
import { i18n } from "../i18n/language"


export default function Template(context) {
  var projectInfo = context.pageContext.project;
  var htmlAst = context.pageContext.htmlAst;
  return <Layout reverse={true}>

    <SEO title={projectInfo.title}
         keywords={projectInfo.keywords}
         description={projectInfo.subtitle}/>

    <Header title={projectInfo.title}
            subtitle={projectInfo.subtitle}
            button={{ href: "", text: "Jetzt lesen" }}>

      <HeaderImage>
        <BubbleBox
          personLeft={require(`../images/person/${require("./../" + projectInfo.author + ".json").image}`)}>
          <Thumbnail projekt={projectInfo.id}/>
        </BubbleBox>
      </HeaderImage>
    </Header>


    <IllustrationBackgroundModern className={"pb-30 bgWhite"}>

      <Container>
        <Blogcontent>
          {htmlAst.children.length >= 1 && <div className={"blogcontent"}>


            <div>
              <div className="blog-post-content">{renderAst(htmlAst)}</div>
            </div>

            <div className={"rightside"}>
              <FancyBox>
                <Person locale={context.pageContext.locale} reverse={true}
                        id={projectInfo.author}
                        person={require("./../" + projectInfo.author + ".json")}
                        style={{
                          "position": "sticky",
                          "top": "20px",
                        }}>
                  <Button href={"/kontakt/"}>Kontakt aufnehmen</Button>
                </Person>
              </FancyBox>

            </div>


          </div>
          }
        </Blogcontent>

      </Container>

      <br/>

      <CTA cta={{
        title: i18n.index.cta.title,
        description: i18n.index.cta.description,
        contact: i18n.index.cta.contact,
        image: "https://webbakers.de/static/team-member-01-6fe955869498f39e33bd4f8bb622187c.jpg"
      }}/>

    </IllustrationBackgroundModern>

  </Layout>
}

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: { "audiofile": Audio, "localimage": Image },
}).Compiler